import { useEffect, useState } from "react";

import "./App.css";

import { Button } from "@mui/material";

function App() {
  const [data, setData] = useState({
    account: "",
    balance: null,
  });
  const [connected, setConnected] = useState(false);

  const ethereum = window.ethereum;

  useEffect(() => {
    if(window.innerWidth<400){
      alert("Working in progress for mobile connect");
    }
    else if (ethereum && ethereum.isMetaMask) {
      ethereum.request({ method: "eth_requestAccounts" }).then((account) => {
        setData({ account: account });
        setConnected(true);
      });
    }
  }, []);

  // To Connect Wallet On Button Click
  const connectWallet = async () => {
    if (ethereum) {
      try {
        const account = await ethereum.request({
          method: "eth_requestAccounts",
        });
        setConnected(true);
        setData({ account: account });

        const networkId = await ethereum.networkVersion;
        console.log(networkId);

        alert(`Connected Sucessfully with ${account}`);

        if (Number(networkId) !== 4) {
          alert(`Please change network To Rinkyby`);
        }
      } catch (error) {
        console.log("Error : ", error.message);

        alert(`Error: ${error.message}`);
      }
    } else {
      alert("Please install Metamask");
    }
  };

  return (
    <div className="App">
      <Button
        variant="contained"
        onClick={connectWallet}
      >{`Connect Metamask`}</Button>

      <h1> {data.account} </h1>
      {console.log(connected)}


    </div>
  );
}

export default App;
